@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@500&display=swap);
.navbar-brand{
  font-family: 'Lemonada', cursive;
  transition: 1s;
}
.navbar-brand:hover, .navbar-brand:focus{
  color: #15de7f !important;
  transition: 1s;
  margin-left: 10px;
}

h2 + hr{
  width: 40px;
  height: 3px;
  background-color: #15de7f;
  float: left;
  margin-top: 0;
  border-radius: 5px;
}

.canais-sugeridos{
  width: auto;
  overflow: hidden;
  z-index: 1;
  height: 290px;
}
.canais-sugeridos > .row{
  width: 205%;
  overflow-y: hidden;
}
.video-card{
  overflow: hidden;
}
.video-card-imagem{
  border-radius: 25px 25px 10px 10px;
  height: 200px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.video-card-videotime{
  float: right;
  margin-top: 160px;
  margin-right: 10px;
  background-color: rgba(36, 37, 40, 0.8);
  border-radius: 5px;
  padding: 3px;
}
.video-card-avatar{
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin-top: 15px;
  float: left;
  position: relative;
}
.video-card-body{
  margin-top: 10px;
  margin-left: 60px;
  position: relative;
  margin-bottom: 20px;
}
.video-card-title{
  display: block;
  font-size: 16px;
  max-height: 25px;
  overflow-y: hidden;
  text-overflow: ellipsis;
  font-weight: bold;
  color: #15de7f;
}
.video-card-channelname{
  display: block;
  font-size: 14px;
  color: #5f6062;
}
.video-card-videoviews{
  display: block;
  font-size: 14px;
  color: #5f6062;
}
.video-card-videoviews::after{
  content: " - Visualizações";
}

/*setas*/
.seta{
  color: transparent;
  font-size: 50px;
  position: absolute;
  margin-top: 70px;
  z-index: 2;
  transition: 2s;
}
.seta:hover{
  color: #15de7f;
  transition: 2s;
}
.seta-esquerda{
  float: left;
  margin-left: -50px;
}
.seta-direita{
  float: right;
  right: -50px;
}


/*parte dos gamers*/
.card-gamer-master{
  height: 300px;
  overflow: hidden;
}
.card-gamer{
  overflow: hidden;
  position:relative;
}
.card-gamer-image{
  border-radius: 25px;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 220px;
}
.card-gamer-nome{
  position: relative;
  display: block;
  text-align: center;
  height: 25px;
  overflow: hidden;
  font-size: 14px;
}
.card-gamer-seguidores{
  text-align: center;
  display: block;
  font-size: 14px;
}
.card-gamer-seguidores::after{
  content: " - seguidores";
}
@media only screen and (max-width: 600px) {
  .seta {
    display: none;
    height: 0;
  }
  .canais-sugeridos{
    height: 250px;
    overflow: hidden;
  }
  .video-card-imagem{
    border-radius: 25px 25px 10px 10px;
    height: 180px;
    max-width: 50%;
  }
  .video-card-videotime{
    float: right;
    margin-top: 90px;
  }

}

body{
  background-color: #242528;
}

